body {
	padding: 0;
	margin: 0;
}
p, ul {
	margin: 0;
}

.Container {
	width: 745px;
	margin-left: auto;
	margin-right: auto;
}


.Header {
	height: 50px;
	background-color: #FFE073;
	position: relative;
}
.Header .Container {
	position: relative;
	height: 100%;
}

.Header__title {
	display: inline-block;
	font-family: Fira Sans;
	font-style: italic;
	font-weight: 500;
	line-height: 50px;
	font-size: 24px;
	color: #254E5B;
}
.Header__button_exit {
	position: relative;
	width: 28px;
	height: 31px;
	top: 50%;
	float: right;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	background-color: #CAD79F;
	border-radius: 5px;
	cursor: pointer;
	-webkit-transition: background-color 0.3s;
	transition: background-color 0.3s;
}
.Header__button_exit:hover {
	background-color: #6AD0C8;
}
.Header__button_exit img {
	width: 24px;
	height: 26px;
	margin-top: 2px;
	margin-left: 2px;
}
.Footer {
  position: absolute;
  width: 921px;
  height: 15px;
  left: calc(50% - 921px/2 - 2.5px);
  bottom: 0;
  background: #60AFA8;
  border-radius: 10px 10px 0px 0px; }


.Body {
  padding-top: 12px; }
  .Body .SearchJoinLine {
    position: relative;
    display: inline-block;
    margin-left: -30px;
    z-index: 14; }
  .Body .MenuIcons {
    display: inline-block; }
  .Body .MyStartup {
    position: relative;
    z-index: 20;
    margin-left: 91px;
    margin-top: -20px; }

.MenuIcons {
  padding: 0;
  margin: 0;
  position: relative;
  border-radius: 5px;
  width: 134px;
  height: 110px;
  background-color: #fff; }
  .MenuIcons li {
    position: absolute;
    text-decoration: none;
    list-style: none;
    z-index: 2;
    -webkit-transition: background-color 0.25s;
    transition: background-color 0.25s; }
    .MenuIcons li:hover {
      background-color: #FFE073; }

.MenuIcons__div_for_circle {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute; }
  .MenuIcons__div_for_circle::after {
    content: '';
    position: absolute;
    left: 57px;
    top: 38px;
    border-radius: 50%;
    width: 145px;
    height: 135px;
    box-sizing: border-box;
    background-color: #fff;
    z-index: 10;
    box-shadow: inset 0px 4px 10px rgba(0, 0, 0, 0.1); }

.MenuIcons__div_for_shadow {
  position: absolute;
  width: 143px;
  height: 200px;
  top: -67px;
  left: -27px;
  overflow: hidden;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg); }
  .MenuIcons__div_for_shadow::after {
    content: '';
    position: absolute;
    border-radius: 5px;
    width: 134px;
    height: 110px;
    top: 44px;
    left: 35px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }

.MenuIcons__startup {
  top: 60px;
  height: 50px;
  width: 80px; }
  .MenuIcons__startup img {
    width: 33px;
    margin-left: 16px;
    margin-top: 7px; }

.MenuIcons__people {
  width: 75px;
  height: 60px;
  border-right: 1px solid rgba(15, 85, 128, 0.5);
  border-bottom: 1px solid rgba(15, 85, 128, 0.5); }
  .MenuIcons__people img {
    width: 33px;
    margin-top: 13px;
    margin-left: 16px; }

.MenuIcons__find {
  left: 75px;
  width: 59px;
  height: 60px; }
  .MenuIcons__find img {
    width: 25px;
    height: 25px;
    margin-top: 10px;
    margin-left: 11px; }

.LineOFConditions {
	margin-top: 20px;
	margin-bottom: 8px;
}
.SearchJoinLine {
	display: inline-block;
}
.SearchField {
	display: inline-block;
	vertical-align: middle;
}
.SearchGamburger {
	display: inline-block;
	margin-left: 20px;
	vertical-align: middle;
}
.SearchButton {
	display: inline-block;
	margin-left: 16px;
	vertical-align: middle;
}
.SearchField {
  position: relative;
  width: 431px;
  height: 30px;
  background-color: #FFE073; }

.SearchField__input {
  position: relative;
  width: 96%;
  height: 80%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: none;
  padding: 0;
  padding-left: 10px;
  background-color: #fff;
  text-decoration: none; }
  .SearchField__input:focus, .SearchField__input:hover {
    outline: none;
    background-color: #F3F3F3; }

.SearchField__div_white1 {
  position: absolute;
  top: 4px;
  left: 0;
  width: 5px;
  height: 11px;
  background-color: #fff; }

.SearchField__div_white2 {
  position: absolute;
  right: 0;
  bottom: 4px;
  width: 5px;
  height: 11px;
  background-color: #fff; }

.SearchGamburger {
	width: 31px;
	height: 25px;
	background: #F3F3F3;
	cursor: pointer;
}
.SearchGamburger div {
	display: block;
	margin-top: 6px;
	margin-left: auto;
	margin-right: auto;
	width: 23px;
	height: 2px;
	background: #84BEBA;
}
.SearchGamburger div:nth-child(1) {
	margin-top: 4px;
}
.SearchButton {
	width: 85px;
	height: 30px;
	padding-left: 11px;
	background: -webkit-linear-gradient(310.91deg, #FFE073 45.91%, #66B489 221.28%);
	background: linear-gradient(139.09deg, #FFE073 45.91%, #66B489 221.28%);
	cursor: pointer;

	font-family: Fira Sans;
	font-style: italic;
	font-weight: 500;
	line-height: 30px;
	font-size: 16px;
	color: #254E5B;
}

.LineOFConditions {
	display: block;
	height: 10px;
	background-color: #F3F3F3;
	-webkit-transition: height 1s;
	transition: height 1s;
}

.LineOFConditions_show_yes {
	height: 100px;
}
.StartupCard {
	margin-right: 36px;
	margin-bottom: 49px;
}
.Startup__popup {
	right: -105%;
	top: -50%;
	display: none;
	-webkit-transition: opacity 0.1s ease;
	transition: opacity 0.1s ease;
	z-index: 20;
}
.StartupCard:hover .Startup__popup {
	display: block;
}

.StartupCard {
	position: relative;
	display: inline-block;
	width: 208px;
