	background-color: #009D90;
}

.StartupCard__triangle {
	position: absolute;
	right: 0;
	bottom: 0;
}
.StartupCard__button {
	position: absolute;
	cursor: pointer;
	height: 41px;
	width: 91px;
	right: -18px;
	bottom: -10px;
	background: -webkit-linear-gradient(304.67deg, #FFE485 14.26%, #009D90 431.65%);
	background: linear-gradient(145.33deg, #FFE485 14.26%, #009D90 431.65%);
	background-color: #F4E185;
	box-shadow: 1px 4px 10px rgba(0, 0, 0, 0.25);
	-webkit-transition: background-color 1.35s;
