	padding-left: 5px;
	font-family: Fira Sans;
	font-style: italic;
	font-weight: 500;
	line-height: 41px;
	font-size: 16px;
	color: #254E5B;
}
.StartupCard__button:hover {
	background: -webkit-linear-gradient(304.67deg, rgba(255, 228, 133, 0.01) 14.26%, rgba(0, 157, 144, 0.01) 431.65%);
	background: linear-gradient(145.33deg, rgba(255, 228, 133, 0.01) 14.26%, rgba(0, 157, 144, 0.01) 431.65%);
	background-color: #FFE485;
}
.Startup__popup {
  position: absolute; }

.popup__main-div {
  position: relative;
  min-height: 80px;
  min-width: 200px;
  padding: 12px 15px;
  background-color: #DDF8F6;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
